@font-face {
  font-family: "lucidagrande";
  src: url("../fonts/lucidagrande.woff2") format("woff2"), url("../fonts/lucidagrande.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "FuturaBookC";
  src: url("../fonts/FuturaBookC.woff2") format("woff2"), url("../fonts/FuturaBookC.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "adelle_sans_regular";
  src: url("../fonts/adelle_sans_regular.woff2") format("woff2"), url("../fonts/adelle_sans_regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "adellecyrillic";
  src: url("../fonts/adellecyrillic.woff2") format("woff2"), url("../fonts/adellecyrillic.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "adelle_sans_italic";
  src: url("../fonts/adelle_sans_italic.woff2") format("woff2"), url("../fonts/adelle_sans_italic.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "adelle_sans_bold";
  src: url("../fonts/adelle_sans_bold.woff2") format("woff2"), url("../fonts/adelle_sans_bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

* {
  box-sizing: border-box;
  outline: none;
}

*:after, *:before {
  box-sizing: inherit;
}

html ::selection {
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
}

html ::-moz-selection {
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
}

html, body {
  height: 100%;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

body {
  color: #000;
  background-color: #fff;
}

* html .page__wrapper {
  height: 100%;
}

a,
button {
  text-decoration: none;
  outline: none;
  border: none;
  cursor: pointer;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0 0 .8em;
  font-weight: normal;
  line-height: 1.1;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.8em;
}

h3 {
  font-size: 1.6em;
}

h4 {
  font-size: 1.5em;
}

h5 {
  font-size: 1.4em;
}

h6 {
  font-size: 1.3em;
}

b, strong {
  font-weight: normal;
}

p {
  margin: 0;
}

.layout {
  position: relative;
}

.layout__wide {
  width: 1240px;
  padding: 0 20px;
  margin: 0 auto;
  max-width: 96%;
}

.section {
  display: block;
}

.title__t {
  font-size: 50px;
  color: #fff;
  line-height: 1.2;
  letter-spacing: -1.04px;
  width: 65%;
  font-family: 'adellecyrillic', Arial, sans-serif;
}

.title__i {
  font-size: 14px;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 2.2em;
  letter-spacing: 0.108em;
  font-family: 'adelle_sans_regular', Arial, sans-serif;
}

.title__m {
  font-size: 19px;
  line-height: 1.4;
  color: #fff;
}

.desc__m {
  font-size: 14px;
  line-height: 1.6;
  color: #fff;
  font-family: 'adelle_sans_regular', Arial, sans-serif;
}

.btn {
  display: inline-block;
  font-weight: 400;
  text-align: center;
  white-space: nowrap;
  vertical-align: top;
  user-select: none;
  border: 1px solid transparent;
  padding: 16px 25px 14px;
  text-transform: uppercase;
  color: #fff;
  font-size: 10px;
  line-height: 1.5;
  letter-spacing: 0.15em;
  font-family: 'adelle_sans_regular', Arial, sans-serif;
  transition: 0.4s;
}

.btn-purpur {
  background-color: #AC218E;
}

.parallax {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.parallax_bg {
  height: 160%;
  position: relative;
  top: 0%;
  left: 0;
  width: 100%;
  will-change: transform;
  background-position: 50% 100%;
  background-repeat: no-repeat;
  background-attachment: scroll;
  backface-visibility: hidden;
  background-size: cover;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 40px 0;
}

.header__wrap {
  position: relative;
  width: 60%;
  align-items: center;
  justify-content: spaace-between;
  backface-visibility: visible;
  perspective-origin: 150% 150%;
  transform-style: preserve-3d;
  transition: 0.6s;
}

.header__wrap.is-active .menu {
  transform: rotateX(90deg);
  transition: 0.6s;
}

.header__wrap.is-active .search {
  width: auto;
}

.header__wrap.is-active .search__input {
  width: 300px;
  max-width: 100%;
  padding: 6px 14px 4px;
  font-size: 14px;
  color: #333;
  background-color: #fff;
  font-family: 'adellecyrillic', Arial, sans-serif;
  transition: 0.6s;
}

.burger {
  display: none;
  position: absolute;
  top: 40px;
  right: 20px;
  width: 40px;
  height: 40px;
  background-color: transparent;
  z-index: 20;
  cursor: pointer;
  transition: all 0.3s ease-out;
}

@media only screen and (max-width: 1020px) {
  .burger {
    display: inline-block;
  }
}

@media only screen and (max-width: 650px) {
  .burger {
    top: 25px;
  }
}

.burger:after {
  position: relative;
  display: block;
  content: ' ';
  top: 5px;
  width: 40px;
  height: 5px;
  background-color: #fff;
  transition: all 0.3s ease-out;
}

.burger:before {
  position: relative;
  display: block;
  content: ' ';
  bottom: 10px;
  width: 40px;
  height: 5px;
  background-color: #fff;
  transition: all 0.3s ease-out;
}

.burger.is-active:before {
  bottom: -5px;
  transform: rotate(45deg);
}

.burger.is-active:after {
  top: -5px;
  transform: rotate(-45deg);
}

.burger.is-active i {
  background-color: transparent;
}

.burger i {
  position: relative;
  display: block;
  top: 50%;
  width: 40px;
  height: 5px;
  background-color: #fff;
  transform: translateY(-50%);
  transition: all 0.3s ease-out;
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.menu__item {
  display: inline-block;
  vertical-align: top;
}

.menu__link {
  text-transform: uppercase;
  display: inline-block;
  vertical-align: top;
  color: #fff;
  font-size: 14px;
  letter-spacing: .2em;
  font-family: 'adelle_sans_bold', Arial, sans-serif;
  transition: 0.4s;
}

.menu__link:hover {
  color: #AC218E;
}

.search {
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  overflow: hidden;
  transition: 0.6s;
}

.search__input {
  display: inline-block;
  vertical-align: top;
  width: 0;
  padding: 0;
  background-color: transparent;
  border: none;
}

.startscreen {
  position: relative;
  display: block;
  height: 100vh;
  min-height: 600px;
}

.startscreen:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
  background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.67));
  pointer-events: none;
}

.startscreen__wrap {
  position: relative;
  z-index: 10;
}

.startscreen__content {
  margin-top: 30px;
  padding-bottom: 80px;
}

.startscreen .layout {
  height: 100%;
}

.startscreen .title__i {
  opacity: .75;
}

.startscreen .title__t {
  margin-bottom: 60px;
}

.startscreen-slider {
  position: absolute;
  bottom: 30px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  padding-top: 35px;
  border-top: 2px solid rgba(255, 255, 255, 0.2);
  z-index: 10;
}

.startscreen-slider__item {
  display: inline-block;
  vertical-align: top;
  width: 31%;
}

.startscreen-slider__pic {
  position: relative;
  display: inline-block;
  vertical-align: top;
  width: 106px;
  height: 106px;
  border-radius: 100%;
  margin-right: 12px;
  overflow: hidden;
}

.startscreen-slider__img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
}

.startscreen-slider__content {
  display: inline-block;
  vertical-align: top;
  width: calc(100% - 124px);
}
